import m from "mithril";
import Chartist from "chartist";
import { Analytics } from "../models/analytics.js";
import format from "date-fns/format";
import "chartist-plugin-tooltip";

function InitializeLineGraph(className, seriesName) {
	let chart = new Chartist.Line(
		className,
		{
			series: [
				{
					name: seriesName,
					data: [{ x: new Date(143134652600), y: 53 }]
				}
			]
		},
		{
			showArea: true,
			axisX: {
				type: Chartist.FixedScaleAxis,
				divisor: Analytics.CurrentChartDivisor,
				scaleMinSpace: 60,
				labelInterpolationFnc: function(value) {
					return format(value, Analytics.CurrentChartFormat);
				}
			},
			plugins: [
				Chartist.plugins.tooltip({
					className: "bg-red dib red chartist-tooltip",
					appendToBody: true
				})
			]
		}
	);

	chart.on("draw", function(data) {
		if (data.type === "line" || data.type === "area") {
			data.element.animate({
				d: {
					begin: 500 * data.index,
					dur: 1000,
					from: data.path
						.clone()
						.scale(1, 0)
						.translate(0, data.chartRect.height())
						.stringify(),
					to: data.path.clone().stringify(),
					easing: Chartist.Svg.Easing.easeOutQuint
				}
			});
		}
	});

	return chart;
}

function UpdateLineGraph(chart, data) {
	chart.data = data;

	chart.options.axisX.type = Chartist.FixedScaleAxis;
	chart.options.axisX.divisor = Analytics.CurrentChartDivisor;
	chart.options.axisX.labelInterpolationFnc = function(value) {
		return format(value, Analytics.CurrentChartFormat);
	};

	chart.update();
}

var Dashboard = {
	oncreate: function({ attrs }) {
		Dashboard.chart1 = InitializeLineGraph(".ct-prod-views", "PRODUCT VIEWS");
		Dashboard.chart2 = InitializeLineGraph(".ct-wishlists", "WISHLIST");
		Dashboard.chart3 = InitializeLineGraph(".ct-orders", "ORDERS");
		Dashboard.chart4 = InitializeLineGraph(".ct-revenue", "REVENUE");

		Dashboard.GetGraphData(attrs);
		Analytics.SingleNumberInfluxAnalytics(attrs.shop_id);
	},
	GetGraphData: function(attrs) {
		Analytics.GetProductViewAnalytics(attrs.shop_id).then(function() {
			console.log(Analytics.MainChartAnalyticsData);
			UpdateLineGraph(Dashboard.chart1, Analytics.ProdViewsChartAnalyticsData);
			UpdateLineGraph(Dashboard.chart2, Analytics.WishListChartAnalyticsData);
			UpdateLineGraph(Dashboard.chart3, Analytics.OrdersChartAnalyticsData);
			UpdateLineGraph(Dashboard.chart4, Analytics.RevenueChartAnalyticsData);
		});
	},
	view: function(vnode) {
		return (
			<section>
				<div class="ph4 pv4 bg-white ba b--light-gray ">
					<div class="">
						<span class="fw6 f3">Dashboard </span>
					</div>
				</div>
				<section>
					<section class="pa3">
						<div class="pv3 tc">
							<div class="pa2 dib w-25">
								<div class="pa2 bg-white shadow-4 ba b--light-gray">
									<strong class="db">Pending Shipment</strong>
									<span class="f2">34</span>
								</div>
							</div>
							<div class="pa2 dib w-25">
								<div class="pa2 bg-white shadow-4 ba b--light-gray">
									<strong class="db">Add to Wishlists</strong>
									<span class="f2">
										{Analytics.SingleCountAnalytics.AddToWishList}
									</span>
								</div>
							</div>
							<div class="pa2 dib w-25">
								<div class="pa2 bg-white shadow-4 ba b--light-gray">
									<strong class="db">Add to Carts</strong>
									<span class="f2">
										{Analytics.SingleCountAnalytics.AddToCartList}
									</span>
								</div>
							</div>
							<div class="pa2 dib w-25">
								<div class="pa2 bg-white shadow-4 ba b--light-gray">
									<strong class="db">Total Orders</strong>
									<span class="f2">
										{Analytics.SingleCountAnalytics.MerchantOrders}
									</span>
								</div>
							</div>
						</div>

						<div class="bg-white pa3 shadow-4 ba b--light-gray ">
							<div id="controls" class="cf pv3">
								<select
									class="pa3 dib fr"
									onchange={m.withAttr("value", value => {
										Analytics.MainChartScale = value;
										Dashboard.GetGraphData(vnode.attrs);
									})}
								>
									<option value="24h">Last 24 hours</option>
									<option value="7d">Last 7 days</option>
									<option value="12w">Last 12 weeks</option>
									<option value="1y">Last 1 year</option>
								</select>
							</div>

							<section>
								<div class="cf">
									<div class="w-50 fl pa3">
										<div>PRODUCT VIEWS: {Analytics.ChartCountAnalytics.productDetailView}</div>
										<div class="ct-chart ct-perfect-fourth ct-prod-views" />
									</div>
									<div class="w-50 fl pa3">
										<div>ADD TO WISHLISTS: {Analytics.ChartCountAnalytics.AddToWishList}</div>
										<div class="ct-chart ct-perfect-fourth ct-wishlists" />
									</div>
								</div>
								<div class="cf">
									<div class="w-50 fl pa3">
										<div>ORDERS: {Analytics.ChartCountAnalytics.MerchantOrdersCount}</div>
										<div class="ct-chart ct-perfect-fourth ct-orders" />
									</div>
									<div class="w-50 fl pa3">
										<div>REVENUE: {Analytics.ChartCountAnalytics.MerchantOrdersRevenue}</div>
										<div class="ct-chart ct-perfect-fourth ct-revenue" />
									</div>
								</div>
							</section>

						</div>
					</section>
				</section>
			</section>
		);
	}
};

export default Dashboard;



