import m from "mithril";
import { Orders } from "../../models/orders.js";

import { MerchantModel } from "../../models/merchant.js";
import format from "date-fns/format";
import Flatpickr from "flatpickr";
import Taggle from "taggle";
// import confirmDatePlugin from "flatpickr/src/plugins/confirmDate/confirmDate.js";
// const flatpickr = require('flatpickr');
import izitoast from "izitoast";

var ViewOrder = {
	oncreate: function(vnode) {
		// get the listing by slug
		Orders.GetShopOrder(vnode.attrs.shop_id, vnode.attrs.order_id).then(
			function() {}
		);
	},
	oninit: function(vnode) {
		console.log("vnodes.attrs: ", vnode.attrs);
	},

	view: function(vnode) {
		// console.log(Listings.Categories)
		return (
			<section>
				<div class="pa3">
					<a > &lt; back to orders</a>
				</div>
				<div class="pa3">
					<strong class="f3">Order Details</strong>
				</div>

				<div class="bb b--light-gray bg-white-90 pv3">
					<div class="cf pa3">
						<div class="dib w-third  pa3 v-top">
							<div class=" ba b--light-gray br2">
								<div class="pa3 tc bb b--light-gray ">
									<strong>Ordered By</strong>
								</div>
								<div class="pa3  ">
								<div class="pa2">
											{Orders.CurrentOrder.user.name}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.user.phone}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.user.email}
										</div>
								</div>
							</div>
						</div>
						<div class="dib w-two-thirds pa3  v-top ">
							<div class=" ba b--light-gray br2 ">
								<div class="pa3 tc bb b--light-gray ">
									<strong>Shipping to:</strong>
								</div>
								<div class="pa3">
									<div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.name}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.company}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.address1}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.address2}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.city +
												", " +
												Orders.CurrentOrder.shipping_info.province +
												", " +
												Orders.CurrentOrder.shipping_info.country}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.postalcode}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.phone}
										</div>
										<div class="pa2">
											{Orders.CurrentOrder.shipping_info.email}
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="tc">
						<div class="dib pa3">Status: Online</div>
					</div>
				</div>

				<section>
					<section class="dib w-two-thirds-ns pa3-ns v-top ">
						<div>
							{Orders.CurrentOrder.items.map(function(item) {
								let imgURL = item.product.images[0].url;
								let imgPlaceholder = item.product.images[0].base64_placeholder;
								return (
									<div class="pa3 ba b--light-gray bg-white-70 cf">
										<div
											class=" overflow-hidden w4 h-auto fl r2 cover mr3"
											oncreate={vnode => {
												vnode.dom.style.height =
													vnode.dom.offsetWidth / 0.7 + "px";
											}}
										>
											<noscript>
												<img
													src={`http://tinyfiles.past3dev.com/resize?width=300&height=0&type=jpeg&nocrop=true&url=${
														imgURL
													}`}
												/>
											</noscript>
											<img
												data-sizes="auto"
												src={imgPlaceholder}
												data-srcset={`${imgPlaceholder} 220w,
                http://tinyfiles.past3dev.com/resize?width=100&height=0&type=jpeg&nocrop=true&url=${
									imgURL
								} 300w,
                http://tinyfiles.past3dev.com/resize?width=200&height=0&type=jpeg&nocrop=true&url=${
									imgURL
								} 600w,
                http://tinyfiles.past3dev.com/resize?width=300&height=0&type=jpeg&nocrop=true&url=${
									imgURL
								} 900w`}
												class="lazyload w-100 h-100 cover "
											/>
										</div>

										<strong class="db pa1">{item.product.title}</strong>
										<span class="db ">{item.product.price}</span>
										<span class="db mt4">QTY: {item.quantity}</span>
									</div>
								);
							})}
						</div>
					</section>

					<section class="dib w-third-ns pa3-ns v-top ">
						<div>
							<div class="pa1">
								<span>Total Order Price</span>
							</div>
							<div class="pv5 tc bg-lightest-blue mb2">
								<strong class="f3">{Orders.CurrentOrder.total}</strong>
								<small>paid</small>
							</div>

							<div class="pt4 pa1">
								<span>Shipping</span>
							</div>
							<div class="pv5 tc bg-light-green mt2">
								<strong class="f3">{Orders.CurrentOrder.shipping}</strong>
								<small>paid</small>
							</div>
						</div>
					</section>
				</section>
			</section>
		);
	}
};

export default ViewOrder;



