import m from "mithril";
import { Listings } from "../models/listings.js";
import format from "date-fns/format";
import izitoast from 'izitoast';
import { MerchantModel } from "../models/merchant.js";
import { Shops } from "../models/shops.js";
import localforage from "localforage";

var SelectShop = {
	state: {

	},
	oncreate: vnode => {
		Shops.GetShops()
	},

	SelectShop:function(shop){
		// console.log("selected shop ",shop)
		localforage.setItem("selected_shop", shop)

	},
  view:function(){
    let shops = Shops.Shops.map(function(shop, key) {
      return (
        <a
          href={'/' + shop.shop_id + '/dashboard' }
          key={key}
					onclick={()=>SelectShop.SelectShop(shop)}
          className="link navy"
					oncreate={m.route.link}
        >
          <div className="bb b--light-gray grow ph3 pv2">

            <h3 className="navy mv1 fw4">{shop.title}</h3>
            <div>
              <div className=" pv1">
                <small>ID:&nbsp;&nbsp;&nbsp;{shop.shop_id}</small>
              </div>
            </div>
          </div>
        </a>
      );
    });

    return (
      <section >
        <section className="tc pt5 vh-100 flex flex-column items-center justify-center">
          <section className="pt4 dib w-100 w-60-m w-40-l tl ">
            <div className="pv3 cf">
              <strong className="dib navy v-btm f4">Select a  Shop</strong>
              <div className="fr  dib">
                <a
									oncreate={m.route.link}
                  href="/shops/create"
                  className="ph3 pv2 ba link navy dib grow"
                >
                  New Shop
                </a>

              </div>
            </div>
            <section className="shadow-4  pa3 bt bw2 b--custom-green">
              {shops}
            </section>
						<div class="tc gray f6 pa2">
							<a class="link" href="/">Go back to homepage</a>
						</div>
          </section>
        </section>
      </section>
    );
  }

};

export default SelectShop;



