import m from "mithril";
import izitoast from "izitoast";

var NotFound = {
	view: function() {
		return (
			<section class="vh-100 vh-100 flex flex-row justify-center items-center">
				<section class="dib v-mid pa3 tc">
					<div class="dib measure ">
						<img src="/assets/img/svg/notfound.svg"/>
						<h2 class="pv4">Requested Resource was not found</h2>
						<a
							href="/"
							oncreate={m.route.link}
							class="pa3 bg-navy white-90 shadow-4 link"
						>
							Back to shops
						</a>
					</div>
				</section>
			</section>
		);
	}
};

export default NotFound;



