import m from "mithril";
import { Orders } from "../../models/orders.js";
import format from "date-fns/format";
import izitoast from "izitoast";
import distanceInWordsToNow from 'date-fns/distance_in_words_to_now'

var OrdersTable = {
	oncreate: function({attrs}) {
		console.log(attrs.shop_id)
		OrdersTable.state.loader = true;
		Orders.GetAllOrders(attrs.shop_id).then(function() {
			OrdersTable.state.loader = false;
		}).catch(function(){
			OrdersTable.state.loader = false;
		})
	},
	state: {
		loader : true
	},
	view: function(vnode) {
		let {shop_id, product_slug} = vnode.attrs;

		return (
			<section >
				<div class="ph4 pv4 bg-white ba b--light-gray ">
					<div class="">
						<span class="fw6 f3">Orders </span>
						<a
							href={"/"+vnode.attrs.shop_id+"/Orders/new"}
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline shadow-4"
							oncreate={m.route.link}
						>
							New Order
						</a>
					</div>
				</div>
				<section class="pa3">
				<div class="pa3 pa4-ns bg-white mt3 cf ba b--light-gray">
					<div class="" style="overflow: auto">
						<table class="f6 w-100 mw8 center" cellspacing="0">
							<thead class="pa2 ">
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">S/N</th>
								{/* <th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">Order ID</th> */}
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Total Items
								</th>
                                <th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Total Price
								</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">Shipping</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Date
								</th>
                                <th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Status
								</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Actions
								</th>
							</thead>
							<tbody class="lh-copy">
								{Orders.AllOrders.length
									? Orders.AllOrders.map(function(Order, i) {
											return (
												<tr class="">
													<td class="pv3 pr3 bb b--black-20 tc">
														{i + 1}
													</td>
													{/* <td class="pv3 pr3 bb b--black-20 tc">
														{Order.order_id}
													</td> */}
                                                    <td class="pv3 pr3 bb b--black-20 tc">
														{Order.items.length}
													</td>

													<td class="pv3 pr3 bb b--black-20 tc">
														<span class="db">
                                                        {Order.total.toLocaleString()}
														</span>
													</td>
                                                    <td class="pv3 pr3 bb b--black-20 tc">
                                                    {Order.shipping.toLocaleString()}
													</td>
													<td class="pv3 pr3 bb b--black-20 tc f6">
                                                        {distanceInWordsToNow(Order.date_created)} ago
													</td>
                                                    <td class="pv3 pr3 bb b--black-20 tc">
                                                        <span class="dib pa1 f6 ma1 bg-washed-blue" >{"not shipped"}</span>
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														<a
															href={`/${shop_id}/orders/${Order.order_id}`}
															class="ph2 pv1 bg-navy white-90 grow pointer no-underline ma1 shadow-4"
															oncreate={m.route.link}
														>
															View
														</a>
														<button
															class="ph2 pv1 bg-navy ba b--transparent white-90 grow pointer no-underline ma1 shadow-4"
															onclick={function(){
																Orders.DeleteOrder(shop_id, Order.slug).then(function(resp){
																	if (resp.title == "Success") {
																		izitoast.success({
																			message: "Order Deleted Successfully",
																			title: "Success",
																			position: "topRight"
																		})
																		Orders.GetAllOrders(vnode.attrs.shop_id).then(function () {
																			m.redraw();
																		})
																	}
																})
															}}
														>
															Delete
														</button>
													</td>
												</tr>
											);
										})
									: ""}
							</tbody>
						</table>
						<div class="tc">
							{OrdersTable.state.loader?<div class="loader" style="color: red"></div>:""}
						</div>
					</div>
					<div class="mt4 cf pa2 pv3">
						<a
							href="#!"
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline mh1 shadow-4"
							oncreate={m.route.link}
						>
							Previous
						</a>
						<a
							href="#!"
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline mh1 shadow-4"
							oncreate={m.route.link}
						>
							Next
						</a>
					</div>
				</div>
			</section>
			</section>
		);
	}
};

export default OrdersTable;



