import { GoogleMapsLoader } from "../../shared/util/maps.js";
var googleMapsLoader = require("google-maps");

//Set up some of our variables.
var map; //Will contain map object.
var marker = false; ////Has the user plotted their location marker?

//Function called to initialize / create the map.
//This is called when the page has loaded.
function initMap() {

		console.log("initMap")
		//The center location of our map.
		var centerOfMap = new google.maps.LatLng(4.9757, 8.3417);

		//Map options.
		var options = {
			center: centerOfMap, //Set center.
			zoom: 7 //The zoom value.
		};

		//Create the map object.
		map = new google.maps.Map(document.getElementById('map'), options);
		marker = new google.maps.Marker({
			// position: clickedLocation,
			position: {lat: 4.9757, lng: 8.3417}, // set the default to calabar...
			map: map,
			draggable: true //make it draggable
	});

				marker.addListener('position_changed', function() {
					m.redraw()
				})
 				console.log(map)
 				var autocomplete = new google.maps.places.Autocomplete(document.getElementById("map-autocomplete"));

				 // Bind the map's bounds (viewport) property to the autocomplete object,
				 // so that the autocomplete requests use the current map bounds for the
				 // bounds option in the request.
				 autocomplete.bindTo('bounds', map);

				 autocomplete.addListener('place_changed', function() {
					// infowindow.close();
					marker.setVisible(false);
					var place = autocomplete.getPlace();
					if (!place.geometry) {
						// User entered the name of a Place that was not suggested and
						// pressed the Enter key, or the Place Details request failed.
						// window.alert("No details available for input: '" + place.name + "'");
						return;
					}

					// If the place has a geometry, then present it on a map.
					if (place.geometry.viewport) {
						map.fitBounds(place.geometry.viewport);
					} else {
						map.setCenter(place.geometry.location);
						map.setZoom(17);  // Why 17? Because it looks good.
					}
					marker.setPosition(place.geometry.location);
					marker.setVisible(true);

					var address = '';
					if (place.address_components) {
						address = [
							(place.address_components[0] && place.address_components[0].short_name || ''),
							(place.address_components[1] && place.address_components[1].short_name || ''),
							(place.address_components[2] && place.address_components[2].short_name || '')
						].join(' ');
					}
					m.redraw()
				});

				//Listen for any clicks on the map.
				google.maps.event.addListener(map, 'click', function(event) {
						//Get the location that the user clicked.
						var clickedLocation = event.latLng;
						//If the marker hasn't been added.
						if(marker === false){
								//Create the marker.
								marker = new google.maps.Marker({
										position: clickedLocation,
										map: map,
										draggable: true //make it draggable
								});
								//Listen for drag events!
								google.maps.event.addListener(marker, 'dragend', function(event){
										markerLocation();
								});
						} else{
								//Marker has already been added, so just change its location.
								marker.setPosition(clickedLocation);
						}
						//Get the marker's location.
						m.redraw()
				});

}

