import m from "mithril";
import localforage from "localforage";
import slideout from "slideout";
import { Analytics } from "../models/analytics.js";
import { MerchantModel } from "../models/merchant.js";
import { menus } from "../models/menus.js";
import { isEmptyObject } from "../../util/utils.js";
import { SVGIcons } from "../../shared/components/svgIcons.js";
import { Shops } from "../models/shops.js";

var MenuComponent = {
	view: function(vnode) {
		return (
			<div class="">
				{menus(vnode.attrs.shop_id).map(function(menuItem, i) {
					let submenus;
					if (menuItem.submenu && menuItem.submenu.length > 0) {
						submenus = menuItem.submenu.map(function(item) {
							return (
								<a
									class="dib w-100 pv1 ph3 hover-bg-light-gray link navy fw2 cf pointer f6"
									href={item.href}
									oncreate={m.route.link}
									key={i}
									onfocus={() => {
										vnode.attrs.slideout ? vnode.attrs.slideout.close() : "";
										// m.route.set(item.href);
									}}
								>
									<span class="dib v-mid ml4 pl3">{item.title}</span>
								</a>
							);
						});
					}
					return (
						<div>
							<a
								class="dib w-100 pv2 ph3 hover-bg-light-gray link navy fw2 cf pointer"
								href={menuItem.href}
								oncreate={m.route.link}
								key={i}
								onfocus={() => {
									vnode.attrs.slideout ? vnode.attrs.slideout.close() : "";
									// m.route.set(menuItem.href);
								}}
							>
								<span class="dib v-mid mr3">
									<img
										class="w1 h1"
										src={"/assets/img/svg/" + menuItem.title + ".svg"}
									/>
								</span>
								<span class="dib v-mid">{menuItem.title}</span>
								<span class="pv1">
									<span
										class="bg-orange white br1 h1 w1 fr f6 tc v-mid"
										style="visibility:hidden"
									>
										1
									</span>
								</span>
							</a>
							<div>{submenus}</div>
						</div>
					);
				})}
			</div>
		);
	}
};

var Channels = {
	oncreate: function() {
		Shops.GetShops();
	},
	view: function() {
		return (
			<div class="ph3 mb3 mt4">
				<p class="">SALES CHANNELS</p>
				{Shops.Shops.map(function(shop, key) {
					return (
						<div class="cf mv2">
							<span class="dib v-mid br1 w2 h2 mr2 fl cf">
								{shop.Logo ? (
									<img class="w-100 h-100" src={shop.Logo} />
								) : (
									<span class="bg-orange white dib br1 v-mid pa2 w2 fl f5 tc">
										{shop.title.slice(0, 1)}
									</span>
								)}
							</span>
							<div class="dib">
								<span class="db">{shop.title}</span>
								<span class="db gray f7">{shop.shop_id}</span>
							</div>
							<a
								href={"/" + shop.shop_id + "/dashboard"}
								oncreate={m.route.link}
								class="ba b--light-gray br2 pa1 w3 tc fr dib v-mid link"
							>
								<span class="dib v-mid">
									<img class="w1 h1" src="/assets/img/svg/edit.svg" />
								</span>
							</a>
						</div>
					);
				})}
			</div>
		);
	}
};

var AdminShell = {
	fixNav: false,
	slideout: {},
	oncreate: function(vnode) {
		AdminShell.slideout = new slideout({
			panel: document.getElementById("panel"),
			menu: document.getElementById("menu"),
			padding: 256,
			tolerance: 70
		});
		vnode.attrs.slideout = AdminShell.slideout;
		MerchantModel.GetUserfromStorage();
	},
	view: function(vnode) {
		return (
			<section>
				<section class="cf" style="min-height: 90vh">
					<section
						id="menu"
						class="fixed dn dib-ns w-25-m w-20-l v-top fl br b--black-20 h-100 z-3 bg-white"
						style="min-height:100vh; overflow-x: unset"
					>
						<div class="relative vh-100">
							<div class="pb5 vh-80">
								<div class="mb2">
									<div class="pa3 f4 fw3">
										<div class=" dib v-mid pa1 h2 w2 ">
											<SVGIcons type="logo-black" class="" />
										</div>
										&nbsp;
										<strong class="pv2 dib v-mid">Shop Manager</strong>
										{/* <small class="ph2 gray" style="font-size:7px;">
											
										</small> */}
									</div>
								</div>
								<div class="dn dibx w-100 pv2 ph3 hover-bg-light-gray navy fw2 cf">
									<span class="dib v-mid mr3">
										<img class="w1 h1" src="/assets/img/svg/search.svg" />
									</span>
									<span class="dib v-mid">Search</span>
								</div>
								<MenuComponent
									{...vnode.attrs}
									oncreate={v => {
										v.attrs.slideout = vnode.attrs.slideout;
									}}
								/>
							</div>

							<div class="absolute bottom-0 ">
								<div class="pb4">
									<Channels />
								</div>
								<div class="bt bb b--black-20 dib w-100 cf">
									<div class="pa3 dib w-90 br b--black-20 fl h3 cf">
										<img
											class="w2 h2 br-100 dib v-mid mr2"
											alt="user"
											src={
												MerchantModel.User
													? MerchantModel.User.image
													: "/assets/img/avatar.jpg"
											}
										/>
										<p class="dib v-mid mv0">
											{MerchantModel.User ? MerchantModel.User.name : ""}
										</p>
										{/* <span class="fr dib v-mid pv2">^</span> */}
									</div>
									<a
										href="#!"
										oncreate={m.route.link}
										class="link f3 fr pv3 w-10 ph2"
									>
										<span class="">&laquo;</span>
									</a>
								</div>
							</div>
						</div>
					</section>
					<section
						id="panel"
						class="dib w-100 w-75-m w-80-l v-top br1 fr bg-light-gray"
						style="min-height:100vh"
					>
						<div class="dn-ns db w-100 w-75-m w-80-l v-top mv1 pa2">
							{/* <span class="bg-white w2 h2 dib"><SVGIcons class="dib" type="logo-black"/> </span> */}
							<span
								class=""
								onclick={() => {
									AdminShell.slideout.toggle();
								}}
							>
								<img src="/assets/img/logo.svg" class="dib v-mid" />
								<span
									class="dib v-mid pa0 pv1 ml1"
									style="border-bottom:2px solid #9FDDE0"
								>
									Shop440
								</span>
							</span>
						</div>
						{m.fragment(vnode.attrs, vnode.children)}

						<footer class="pa3 bg-light-gray tc mt4">
							<p class="mb2 f6">
								Copyright {new Date().getFullYear()} Shop440{" "}
							</p>
							<small style="font-size:0.5rem">
								Built By{" "}
								<a href="http://past3.com.ng" target="_blank">
									Past3 Technologies
								</a>{" "}
								All rights reserved.
							</small>
						</footer>
					</section>
				</section>
			</section>
		);
	}
};
export default AdminShell;



