import m from "mithril";
import localforage from "localforage";
import { MerchantModel, getCookie } from "../models/merchant.js";

export var MerchantAuth = {
	oncreate: function() {
		MerchantModel.GetUserfromStorage();
	},
	view: function(vnode) {
		var cookie = getCookie("jwt");
		if (cookie === "") {
			// m.route.set("/authenticate/");
			location.assign("/authenticate/");
			return m("div");
		}

		return m("div", vnode.attrs, m.fragment(vnode.attrs, [vnode.children]));
	}
};

export var ShopChecker = {
	oncreate: function() {
		MerchantModel.GetShopFromStorage();
	},
	view: function(vnode) {
		// var cookie = getCookie("jwt");
		// if (cookie === "") {
		// 	// m.route.set("/authenticate/");
		// 	location.assign("/authenticate/");
		// 	return m("div");
		// }

		return m("div", vnode.attrs, m.fragment(vnode.attrs, [vnode.children]));
	}
};



