import m from "mithril";
import izitoast from "izitoast";

var CustomSite = {
  CreateSite:function(shop_id:String){
    console.log(shop_id)
    return m
			.request({
				method: "GET",
				 url: `/api/customsite/${shop_id}/create_site`,
			})
			.then(function(response) {
				console.log(response)
        izitoast.success({
          message: "Site Created Successfully",
          title: "Success",
          position: "topRight"
        })
        return response
			});
  },
  AddCustomDomain:function(shop_id:String, domain:String){
    console.log(shop_id, domain)
    return m
			.request({
				method: "GET",
				 url: `/api/customsite/${shop_id}/add_custom_domain?domain=${domain}`,
			})
			.then(function(response) {
				console.log(response)
        izitoast.success({
          message: "Domain Added Successfully",
          title: "Success",
          position: "topRight"
        })
        return response
			});
  }
}

export default CustomSite;



