import m from "mithril";
import izitoast from "izitoast";
import CustomSite from "../../models/customSite.js";
import { Shops } from "../../models/shops.js";

var CustomDomainIntegration = {
  domain:"",
	oncreate: function({ attrs }) {
		Shops.GetShop(attrs.shop_id);
	},
	view: function({ attrs: { shop_id } }) {
		console.log(shop_id);
		let Content;
		if (
			(Shops.ShopData.shop_plan === "basic" ||
				Shops.ShopData.shop_plan === "value") &&
			(!Shops.ShopData.domain || Shops.ShopData.domain === "")
		) {
			Content = (
				<div class="dib measure mt5">
					<h3>Create a custom site to continue</h3>
					<p class="lh-copy">
						Note, this would automatically upgrade you to the premium plan,
						which attracts a <strong>5000</strong> monthly fee. Make sure you
						have already purchased a domain name with a domain registrar or
						contact shop440 support for assistance
					</p>
					<p class="lh-copy">
						Insert the purchased domain name in the field below to continue
					</p>
					<div class="pa3 ">
						<input
							class="pv2 ph3 ba b--gray w-100 w-80-ns"
							oninput={m.withAttr(
								"value",
								v => {console.log(v); CustomDomainIntegration.domain = v;}
							)}
						/>
					</div>
					<button
						class="pa3 bg-navy white-90 shadow-4 pointer"
						onclick={() =>
							CustomSite.AddCustomDomain(
								shop_id,
								CustomDomainIntegration.domain
							).then(() => {
								Shops.GetShop(shop_id);
								m.redraw();
							})
						}
					>
						Continue
					</button>
				</div>
			);
		} else if (Shops.ShopData.shop_plan === "premium") {
			Content = (
				<section>
					<h2>Connection Info</h2>
          <p>
            To link your domain to shop 440, login to your domain provider and edit your dns records.
          </p>
          <p>
            Create an <strong>A Name</strong> record pointing your domain at <strong>35.205.78.240 </strong>
          </p>
				</section>
			);
		}

		return (
			<section>
				<div class="ph4 pv4 bg-white ba b--light-gray ">
					<div class="">
						<span class="fw6 f3">Custom Site  Overview </span>
					</div>
				</div>
				<section class="pa3 tc">{Content}</section>
			</section>
		);
	}
};

export default CustomDomainIntegration;



