import m from "mithril";

export var Analytics = {
	MainChartScale: "24h",
	CurrentChartFormat: "HH:mm",
	CurrentChartDivisor: 12,
	ProdViewsChartAnalyticsData: { series: [] },
	WishListChartAnalyticsData: { series: [] },
	CartListChartAnalyticsData: { series: [] },
	OrdersChartAnalyticsData: { series: [] },
	RevenueChartAnalyticsData: { series: [] },
	ChartCountAnalytics: {
		AddToCartList: 0,
		AddToWishList: 0,
		productDetailView: 0,
		MerchantOrdersCount: 0,
		MerchantOrdersRevenue: 0
	},
	SingleCountAnalytics: {
		AddToCartList: 0,
		AddToWishList: 0,
		productDetailView: 0,
		MerchantOrders: 0
	},
	Data: {
		UnapprovedListingsCount: 0,
		ListingsCount: 0,
		UsersCount: 0
	},
	GetProductViewAnalytics: shopid => {
		// shopid = "belofawillartandcraftshop.";
		let LastStop, GroupByTime;
		switch (Analytics.MainChartScale) {
			case "24h":
				LastStop = "1d";
				GroupByTime = "1h";
				Analytics.CurrentChartDivisor = 24;
				Analytics.CurrentChartFormat = "HH:mm a";
				break;
			case "7d":
				LastStop = "7d";
				GroupByTime = "1d";
				Analytics.CurrentChartDivisor = 7;
				Analytics.CurrentChartFormat = "MMM D";
				break;
			case "12w":
				LastStop = "12w";
				GroupByTime = "1w";
				Analytics.CurrentChartDivisor = 12;
				Analytics.CurrentChartFormat = "MMM D";
				break;
			case "1y":
				LastStop = "144w";
				GroupByTime = "4w";
				Analytics.CurrentChartDivisor = 12;
				Analytics.CurrentChartFormat = "MMM";
				break;
		}
		let query = encodeURI(`
			SELECT count("slug") AS "count_slug" FROM shop440.autogen.productDetailView,
			shop440.autogen.AddToCartList,
			shop440.autogen.AddToWishList
			 WHERE time > now() - ${LastStop} AND "shopid"='${shopid}' GROUP BY time(${
			GroupByTime
		}), "shopid" FILL(null)`);
		m
			.request({
				method: "GET",
				url: `/api/analytics/db/query?db=shop440&q=${query}`
			})
			.then(function(response) {
				console.log(response);
				Analytics.CartListChartAnalyticsData = { series: [] };
				Analytics.WishListChartAnalyticsData = { series: [] };
				Analytics.ProdViewsChartAnalyticsData = { series: [] };

				Analytics.ChartCountAnalytics.AddToCartList = 0;
				Analytics.ChartCountAnalytics.AddToWishList = 0;
				Analytics.ChartCountAnalytics.productDetailView = 0;

				response.results[0].series.map(function(seriesItem) {
					let yCount = 0;
					let data = seriesItem.values.map(function(d) {
						let yValue = 0;
						if (d[1] !== null) {
							yValue = d[1];
						}
						yCount += yValue;
						return { x: new Date(d[0]), y: yValue, meta: "xxx" };
					});

					let seriesName;
					let s = {
						name: seriesName,
						data: data
					};
					switch (seriesItem.name) {
						case "AddToCartList":
							seriesName = "Items Added To Cart";
							Analytics.ChartCountAnalytics.AddToCartList = yCount;
							Analytics.CartListChartAnalyticsData.series.push(s);
							break;
						case "AddToWishList":
							seriesName = "Items Added To Wishlist";
							Analytics.ChartCountAnalytics.AddToWishList = yCount;
							Analytics.WishListChartAnalyticsData.series.push(s);
							break;
						case "productDetailView":
							seriesName = "Product Views";
							Analytics.ChartCountAnalytics.productDetailView = yCount;
							Analytics.ProdViewsChartAnalyticsData.series.push(s);
							break;
					}
				});

				m.redraw()
			})
			.catch(function(error) {
				console.error(error);
			});

		query = encodeURI(`
				SELECT sum("total_items") AS "count_total_items",
				sum("total_price") AS "sum_total_price" FROM shop440.autogen.MerchantOrders  WHERE time > now() - ${
					LastStop
				} AND "shopid"='${shopid}' GROUP BY time(${
			GroupByTime
		}), "shopid" FILL(null)`);
		return m
			.request({
				method: "GET",
				url: `/api/analytics/db/query?db=shop440&q=${query}`
			})
			.then(function(response) {
				console.log("merchant orders", response);
				Analytics.OrdersChartAnalyticsData = { series: [] };
				Analytics.RevenueChartAnalyticsData = { series: [] };
				Analytics.ChartCountAnalytics.MerchantOrdersCount = 0;
				Analytics.ChartCountAnalytics.MerchantOrdersRevenue = 0;

				response.results[0].series.map(function(seriesItem) {
					let count_total_items = seriesItem.values.map(function(d) {
						let yValue = 0;
						if (d[1] !== null) {
							yValue = d[1];
						}
						Analytics.ChartCountAnalytics.MerchantOrdersCount += yValue;
						return { x: new Date(d[0]), y: yValue, meta: "xxx" };
					});

					let sum_total_price = seriesItem.values.map(function(d) {
						let yValue = 0;
						if (d[2] !== null) {
							yValue = d[2];
						}
						Analytics.ChartCountAnalytics.MerchantOrdersRevenue += yValue;
						return { x: new Date(d[0]), y: yValue, meta: "xxx" };
					});

					let count_total_items_series = {
						name: "",
						data: count_total_items
					};

					let sum_total_price_series = {
						name: "",
						data: sum_total_price
					};

					switch (seriesItem.name) {
						case "MerchantOrders":
							Analytics.OrdersChartAnalyticsData.series.push(
								count_total_items_series
							);
							Analytics.RevenueChartAnalyticsData.series.push(
								sum_total_price_series
							);
							break;
					}
				});

				m.redraw()
			})
			.catch(function(error) {
				console.error(error);
			});
	},

	SingleNumberInfluxAnalytics: shopid => {
		// shopid = "belofawillartandcraftshop.";
		let LastStop, GroupByTime;

		let query = encodeURI(`
			SELECT count("slug") AS "count_slug" FROM shop440.autogen.productDetailView,
			shop440.autogen.AddToCartList,
			shop440.autogen.AddToWishList
			WHERE "shopid"='${shopid}'
			 `);

		let merchant_query = encodeURI(`
			SELECT count("total_items") FROM shop440.autogen.MerchantOrders WHERE "shopid"='${
				shopid
			}'
			 `);

		m
			.request({
				method: "GET",
				url: `/api/analytics/db/query?db=shop440&q=${merchant_query}`
			})
			.then(function(response) {
				console.log(response);
				response.results[0].series.map(function(s) {
					console.log(s.name);
					if (s.name == "MerchantOrders") {
						console.log(s.name);
						Analytics.SingleCountAnalytics.MerchantOrders = s.values[0][1];
					}
				});
			})
			.catch(function(error) {
				console.error(error);
			});

		return m
			.request({
				method: "GET",
				url: `/api/analytics/db/query?db=shop440&q=${query}`
			})
			.then(function(response) {
				console.log(response);
				response.results[0].series.map(function(s) {
					Analytics.SingleCountAnalytics[s.name] = s.values[0][1];
				});
			})
			.catch(function(error) {
				console.error(error);
			});
	}
};



