import m from "mithril";

import { IMAGESERVER } from "../../shared/constants.js";

export type Geo = {
	accuracy?: string,
	lat?: string,
	location?: string,
	lon?: string
};
export type Shop = {
	address?: string,
	date_created?: string,
	date_modified?: string,
	description?: string,
	geo?: Geo,
	logo?: string,
	not_on_trial?: string,
	owner?: string,
	phone?: string,
	shop_id?: string,
	shop_plan?: string,
	specialization?: string,
	title?: string
};

export type ShopsModelType = {
    ShopData?:Shop,
}


export var Shops:ShopModelType = {
	Shops: [],
	ShopData: {},
	CreateShop: function() {
		console.log(Shops.ShopData);
		return m
			.request({
				method: "POST",
				url: "/api/shops",
				data: Shops.ShopData
			})
			.then(function(response) {
				m.route.set("/shops");
			})
			.catch(function(error) {
				console.error(error);
			});
	},
	UploadFile: function(e) {
		return new Promise(function(resolve, reject) {
			var files = e.target.files || e.dataTransfer.files;
			let file = files[0];
			var reader = new FileReader();
			reader.onload = (() => {
				return e => {
					m
						.request({
							method: "POST",
							url: `${IMAGESERVER}/upload`,
							data: {
								Type: "image",
								Content: e.target.result
							}
						})
						.then(function(resp) {
							Shops.ShopData.logo = resp.url;
							resolve(resp);
						})
						.catch(function(err) {
							reject(err);
						});
				};
			})();
			reader.readAsDataURL(file);
		});
	},

	CheckShopIDAvailability: function(shop_id) {
		return m.request({
			method: "POST",
			url: "/api/shops/shopidavailability",
			data: { shop_id: shop_id }
		});
	},
	GetShops: function() {
		return m
			.request({
				method: "GET",
				url: "/api/shops"
			})
			.then(function(response) {
				Shops.Shops = response.Shops;
			})
			.catch(function(error) {
				console.error(error);
			});
	},
	GetShop: function(shopid) {
		return m
			.request({
				method: "GET",
				url: `/api/shops/${shopid}`
			})
			.then(function(response) {
				console.log(response);
				Shops.ShopData = response;
			})
			.catch(function(error) {
				console.error(error);
			});
	}
};



