import m from "mithril";
import izitoast from "izitoast";
import CustomSite from "../../models/customSite.js";
import { Shops } from "../../models/shops.js";
import {DashboardComponent} from "./dashboardComponent.js";

var Overview = {
	oncreate: function({ attrs }) {
		Shops.GetShop(attrs.shop_id);
	},
	view: function({ attrs: { shop_id } }) {
		let Content;
		if (Shops.ShopData.shop_plan === "basic") {
			Content = (
				<div class="dib measure mt5">
					<h3>Create a custom site to continue</h3>
					<p class="lh-copy">
						Note, this would automatically upgrade you to the value plan, which
						attracts a 2500 monthly fee. Your resulting site would be
						&lt;shopid&gt;.shop440.com
					</p>
					<button
						class="pa3 bg-navy white-90 shadow-4 pointer"
						onclick={() =>
							CustomSite.CreateSite(shop_id).then(() =>{
								Shops.GetShop(attrs.shop_id)
								m.redraw()
							}
							)
						}
					>
						Create site
					</button>
				</div>
			);
		}else if (Shops.ShopData.shop_plan === "value" || Shops.ShopData.shop_plan === "premium" ) {
			Content = <DashboardComponent />
		}

		return (
			<section>
				<div class="ph4 pv4 bg-white ba b--light-gray ">
					<div class="">
						<span class="fw6 f3">Custom Site  Overview </span>
					</div>
				</div>
				<section class="pa3 tc">{Content}</section>
			</section>
		);
	}
};

export default Overview;



