import m from "mithril";

// import SignupPage from './containers/signupPage.js';
import OffCanvasMenu from "./components/offCanvasMenu.js";
import AdminShell from "./containers/adminShell.js";

import Dashboard from "./containers/dashboard.js";

import ListingsTable from "./containers/listings/listings.js";
import SelectShop from "./containers/selectShop.js";
import CreateShop from "./containers/createShop.js";
import NewListing from "./containers/listings/newListing.js";
import ViewListing from "./containers/listings/viewListing.js";
import Stats from "./containers/stats/stats.js";
import { MerchantAuth, ShopChecker } from "./components/auth.js";

import Orders from "./containers/orders/orders.js";
import ViewOrder from "./containers/orders/viewOrder.js";
import { MerchantModel } from "./models/merchant";

import CustomSiteOverview from "./containers/customWebsite/overview.js";
import CustomDomainIntegration from "./containers/customWebsite/customDomainIntegration.js";
import NotFound from "./containers/notfound.js";

var root = document.getElementById("appContainer");

m.route.prefix("/merchants");
m.route(root, "/notfound", {
	"/": {
		view: function(vnode) {
			return m(MerchantAuth, vnode.attrs, m(SelectShop, vnode.attrs));
		}
	},
	"/shops/create": {
		view: function(vnode) {
			return m(MerchantAuth, vnode.attrs, m(CreateShop, vnode.attrs));
		}
	},
	"/logout": {
		view: function(vnode) {
			MerchantModel.Logout();
			// return m(MerchantAuth)
		}
	},
	"/:shop_id/dashboard": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(Dashboard, vnode.attrs))
			);
		}
	},
	"/:shop_id/conversations": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(ListingsTable, vnode.attrs))
			);
		}
	},
	"/:shop_id/stats": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(Stats, vnode.attrs))
			);
		}
	},
	"/:shop_id/listings": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(ListingsTable, vnode.attrs))
			);
		}
	},
	"/:shop_id/orders": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(Orders, vnode.attrs))
			);
		}
	},
	"/:shop_id/orders/:order_id": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(ViewOrder, vnode.attrs))
			);
		}
	},
	"/:shop_id/listings/new": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(NewListing, vnode.attrs))
			);
		}
	},
	"/:shop_id/:slug": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(ViewListing, vnode.attrs))
			);
		}
	},
	"/:shop_id/customsite/overview": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(CustomSiteOverview, vnode.attrs))
			);
		}
	},
	"/:shop_id/customsite/domain_integration": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,
				m(AdminShell, vnode.attrs, m(CustomDomainIntegration, vnode.attrs))
			);
		}
	},
	"/notfound": {
		view: function(vnode) {
			return m(
				MerchantAuth,
				vnode.attrs,

				m(NotFound, vnode.attrs)
			);
		}
	}
});



