import m from "mithril";

import {IMAGESERVER} from "../../shared/constants.js";

export var Orders = {
	AllOrders: [],
	NewOrder: {},
	CurrentOrder: {items:[], shipping_info:{}, user:{}},
	GetAllOrders: function(shopid) {
		return m
			.request({
				method: "GET",
				 url: `/api/orders/${shopid}`,
			})
			.then(function(response) {
					Orders.AllOrders = response.data;
					m.redraw();
			});
	},
	UpdateOrder: function(shop_id, product_slug){
		Orders.CurrentOrder.form_factor = document.querySelector('input[name="form_factor"]:checked').value;
		Orders.CurrentOrder.tags = Orders.tags.getTagValues();
		Orders.CurrentOrder.materials = Orders.materials.getTagValues();

		return m.request({
			method: "PUT",
			url: `/api/orders/${shop_id}/${product_slug}`,
			data: Orders.CurrentOrder
		})
		.then(function(response){
			console.log(response);
		}).catch(function(error){
			console.log(error);
		})
	},
	GetShopOrder: function(shop_id, order_id) {
		return m
		  .request({
			method: "GET",
			url: `/api/orders/${shop_id}/orders/${order_id}`
		  })
		  .then(response => {
			Orders.CurrentOrder = response;
			m.redraw();
		  })
		  .catch(error => {
			console.error("featured promos error: ", error);
		  });
	},
	DeleteOrder: function(shop_id, product_slug) {
		return m.request({
			method: "DELETE",
			url: `/api/orders/${shop_id}/${product_slug}`
		}).then(function(response) {
			return response;
		}).catch(function(error) {
			console.log("Error: ", error);
			return error
		})
	},

};



