import m from "mithril";

import {IMAGESERVER} from "../../shared/constants.js";

export var Listings = {
	itemID: {},
	AllListings: [],
	NewListing: {},
	CurrentListing: {},
	Categories: [],
	Uploaded: false,
	GetAllListings: function(shopid) {
		return m
			.request({
				method: "GET",
				 url: `/api/products/${shopid}`,
			})
			.then(function(response) {
				console.log("get listing Response: ", response);
				Listings.AllListings = response;
				m.redraw();
				// if successful, add the new listing to the listing list
			});
	},
	UpdateListing: function(shop_id, product_slug){
		Listings.CurrentListing.form_factor = document.querySelector('input[name="form_factor"]:checked').value;
		Listings.CurrentListing.tags = Listings.tags.getTagValues();
		Listings.CurrentListing.materials = Listings.materials.getTagValues();

		console.log("Update a listing")
		return m.request({
			method: "PUT",
			url: `/api/products/${shop_id}/${product_slug}`,
			data: Listings.CurrentListing
		})
		.then(function(response){
			console.log(response);
		}).catch(function(error){
			console.log(error);
		})
	},
	GetListing: function(shop_id, product_slug) {
		console.log(shop_id, product_slug)
		return m
		  .request({
			method: "GET",
			url: `/api/products/${shop_id}/${product_slug}`
		  })
		  .then(response => {
			console.log("product: ", response);
			var len = response.images.length;
			for (var j = 0; j < len; j++){
				console.log("response.images[j]: ", response.images[j]);
				if (response.images[j].path && Listings.PhotoUploadBoxes[j]) {
					// console.log("Listings.PhotoUploadBoxes["+j+"]: ", Listings.PhotoUploadBoxes[j])
					Listings.PhotoUploadBoxes[j].Image = response.images[j];
				}
			}
			// this will make the "Add new photo" show on the next photo upload spot
			Listings.PhotoUploadSelected = len;
			Listings.CurrentListing = response;
			console.log("PhotoUploadBox: ", Listings.PhotoUploadBoxes)
			m.redraw();
		  })
		  .catch(error => {
			console.error("featured promos error: ", error);
		  });
	},
	DeleteListing: function(shop_id, product_slug) {
		return m.request({
			method: "DELETE",
			url: `/api/products/${shop_id}/${product_slug}`
		}).then(function(response) {
			console.log("Delete listing response: ", response);
			return response;
		}).catch(function(error) {
			console.log("Error: ", error);
			return error
		})
	},
	SaveNew: function(shopid) {
		// TODO:: Save a new listing.
		console.log(Listings.PhotoUploadBoxes)
		let images = Listings.PhotoUploadBoxes.map(function(img){
			if (img.Image && img.Image.url!==""){
				return img.Image
			}
		})

		console.log(images.length);

		Listings.NewListing.images = images
		Listings.NewListing.tags = Listings.tags.getTagValues()
		Listings.NewListing.materials = Listings.materials.getTagValues()

		Listings.NewListing.form_factor = document.querySelector('input[name="form_factor"]:checked').value;
		console.log(Listings.NewListing)
		return m
			.request({
				method: "POST",
				url: `/api/products/${shopid}`,
				data: Listings.NewListing
			})
			.then(function(response) {
				console.log("New listing Response: ", response);
				// incase of adding new listings, no photo has been uploaded...
				Listings.Uploaded = false;
				return response;
				// m.route.set("/listings/view/" + response.slug);
			});
	},
	GetCategories: function() {
		return m
			.request({
				method: "GET",
				url: "/api/categories"
			})
			.then(function(response) {
				Listings.Categories = response;
			})
			.catch(function(error) {
				console.error(error);
			});
	},
	UpdateFile: function(e, index) {
		return new Promise(function(resolve, reject){

			var files = e.target.files || e.dataTransfer.files;

			let file = files[0];
			var reader = new FileReader();
			reader.onload = (() => {
				return e => {

					m.request({
						method:"POST",
						url:`${IMAGESERVER}/upload`,
						data: {
							Type:"image",
							Content:e.target.result,
						}
					}).then(function(resp){
						console.log(Listings.PhotoUploadBoxes[index])
						Listings.PhotoUploadBoxes[index].Image = resp
						if (Listings.CurrentListing.images) {
							Listings.CurrentListing.images[index] = resp;
						}
						// stop loader...
						// Listings.PhotoUploadBoxes[index].uploading = false;
						// at least one of the images has been uploaded...
						// Listings.Uploaded = true;
						m.redraw()
						resolve(resp)
					})
					.catch(function(err){
						reject(err)
					})
				};
			})();
			reader.readAsDataURL(file);
		})
	},
	UploadFile:function(e){
		console.log("Selected: ", Listings.PhotoUploadSelected)
		var files = e.target.files || e.dataTransfer.files;

		let file = files[0];
		var reader = new FileReader();
		reader.onload = (() => {
			return e => {

				if (Listings.PhotoUploadSelected<8){
					Listings.PhotoUploadSelected = Listings.PhotoUploadSelected + 1;
					m.redraw();
				}

				Listings.PhotoUploadBoxes[Listings.PhotoUploadSelected-1].uploading = true;

				m.request({
					method:"POST",
					url:`${IMAGESERVER}/upload`,
					data: {
						Type:"image",
						Content:e.target.result,

					}
				}).then(function(resp){
					console.log(Listings.PhotoUploadBoxes[Listings.PhotoUploadSelected-1])
					Listings.PhotoUploadBoxes[Listings.PhotoUploadSelected-1].Image = resp
					if (Listings.CurrentListing.images) {
						Listings.CurrentListing.images[Listings.PhotoUploadSelected-1] = resp;
					}
					console.log(Listings.PhotoUploadBoxes[Listings.PhotoUploadSelected-1])
					// stop loader...
					Listings.PhotoUploadBoxes[Listings.PhotoUploadSelected-1].uploading = false;
					// at least one of the images has been uploaded...
					Listings.Uploaded = true;
					m.redraw()
				})
			};
		})();
		reader.readAsDataURL(file);
	},
	PhotoUploadSelected:0,
	PhotoUploadBoxes: [{

	},{
		Kind: "Primary photo",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-1.0000.svg"
	},{
		Kind: "Secondary photo",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-2.0000.svg"
	},{
		Kind: "Tertiary",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-3.0000.svg"
	},{
		Kind:"In use",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-6.0000.svg"
	},{
		Kind:"Size and scale",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-7.0000.svg"
	},{
		Kind:"Styled Scene",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-8.0000.svg"
	},{
		Kind:"Variations",
		PlaceholderImage:"/assets/img/svg/addlisting/placeholder-9.0000.svg"
	}],
};



