import m from "mithril";
import { Listings } from "../../models/listings.js";
import format from "date-fns/format";
import izitoast from "izitoast";

var ListingsTable = {
	oncreate: function({attrs}) {
		console.log(attrs.shop_id)
		ListingsTable.state.loader = true;
		Listings.GetAllListings(attrs.shop_id).then(function() {
			ListingsTable.state.loader = false;
		}).catch(function(){
			ListingsTable.state.loader = false;
		})
	},
	state: {
		loader : true
	},
	view: function(vnode) {
		let {shop_id, product_slug} = vnode.attrs;

		return (
			<section >
				<div class="ph4 pv4 bg-white ba b--light-gray ">
					<div class="">
						<span class="fw6 f3">Listings </span>
						<a
							href={"/"+vnode.attrs.shop_id+"/listings/new"}
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline shadow-4"
							oncreate={m.route.link}
						>
							New Listing
						</a>
					</div>
				</div>
				<section class="pa3">
				<div class="pa3 pa4-ns bg-white mt3 cf ba b--light-gray">
					<div class="" style="overflow: auto">
						<table class="f6 w-100 mw8 center" cellspacing="0">
							<thead class="pa2 ">
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc"></th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">Title</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Category
								</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">Price</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Quantity
								</th>
								<th class="fw6 bb b--black-20 tl pb3 pr3 bg-white tc">
									Actions
								</th>
							</thead>
							<tbody class="lh-copy">
								{Listings.AllListings.length
									? Listings.AllListings.map(function(listing, i) {
										let imgURL = listing.images ? listing.images[0].url : "";
										let imgPlaceholder = listing.images
											? listing.images[0].base64_placeholder
											: "";
										let predominant_color = listing.images[0].predominant_color;
											return (
												<tr class="">
													<td class="pv3 pr3 bb b--black-20 tc w4">
														<div
															class="w-100 overflow-hidden cover"
															style={`background-image:url(${imgPlaceholder}); background-color:${predominant_color}`}
															oncreate={vnode => {
																let imgHeight = vnode.dom.offsetWidth / 0.8 + "px";
																vnode.dom.style.height = imgHeight;
															}}
														>
															<img
																data-sizes="auto"
																src={`https://tinyfiles.past3dev.com/resize?width=100&height=0&type=jpeg&nocrop=true&url=${
																	imgURL
																}`}
																data-srcset={`${imgPlaceholder} 220w,
									                https://tinyfiles.past3dev.com/resize?width=100&height=0&type=jpeg&nocrop=true&url=${
																		imgURL
																	} 300w,
									                https://tinyfiles.past3dev.com/resize?width=200&height=0&type=jpeg&nocrop=true&url=${
																		imgURL
																	} 600w`}
																class="lazyload w-100 h-100 cover br2"
															/>
														</div>
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														{listing.title}
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														{listing.category}
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														<span class="db">
															{listing.price}
														</span>
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														{listing.initial_quantity}
													</td>
													<td class="pv3 pr3 bb b--black-20 tc">
														<a
															href={`/${shop_id}/${listing.slug}`}
															class="ph2 pv1 bg-navy white-90 grow pointer no-underline ma1 shadow-4"
															oncreate={m.route.link}
														>
															View
														</a>
														<button
															class="ph2 pv1 bg-navy ba b--transparent white-90 grow pointer no-underline ma1 shadow-4"
															onclick={function(){
																izitoast.question({
																timeout: false,
																close: false,
																overlay: true,
																toastOnce: true,
																id: 'question',
																zindex: 999,
																title: 'Wait',
																message: 'Are you sure about that?',
																position: 'center',
																buttons: [
																['<button>YES</button>', function (instance, toast) {
																						Listings.DeleteListing(shop_id, listing.slug).then(function(resp){
																								if (resp.title == "Success") {
																										instance.hide(toast, { transitionOut: 'fadeOut' }, 'button');
																										izitoast.success({
																											message: "Listing Deleted Successfully",
																											title: "Success",
																											position: "topRight"
																										})
																										Listings.GetAllListings(vnode.attrs.shop_id).then(function () {
																												m.redraw();
																										})
																								}
																						})
																}, true],
																['<button>NO</button>', function (instance, toast) {
																instance.hide(toast, { transitionOut: 'fadeOut' }, 'button');
																}]
																],
																onClosing: function(instance, toast, closedBy){
																console.info('Closing | closedBy: ' + closedBy);
																},
																onClosed: function(instance, toast, closedBy){
																console.info('Closed | closedBy: ' + closedBy);
																}
																});

															}}
														>
															Delete
														</button>
													</td>
												</tr>
											);
										})
									: ""}
							</tbody>
						</table>
						<div class="tc">
							{ListingsTable.state.loader?<div class="loader" style="color: red"></div>:""}
						</div>
					</div>
					<div class="mt4 cf pa2 pv3">
						<a
							href="#!"
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline mh1 shadow-4"
							oncreate={m.route.link}
						>
							Previous
						</a>
						<a
							href="#!"
							class="fr ph3 pv2 bg-navy white-90 grow pointer no-underline mh1 shadow-4"
							oncreate={m.route.link}
						>
							Next
						</a>
					</div>
				</div>
			</section>
			</section>
		);
	}
};

export default ListingsTable;



