import m from "mithril";
import localforage from "localforage";

export function getCookie(cname) {
	var name = cname + "=";
	var decodedCookie = decodeURIComponent(document.cookie);
	var ca = decodedCookie.split(";");
	for (var i = 0; i < ca.length; i++) {
		var c = ca[i];
		while (c.charAt(0) == " ") {
			c = c.substring(1);
		}
		if (c.indexOf(name) == 0) {
			return c.substring(name.length, c.length);
		}
	}
	return "";
}
export function deleteCookie(name) {
	document.cookie = name + "=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;";
}

export var MerchantModel = {
	User: {},
	Shops: [],

	GetUserfromStorage: function() {
		if (!MerchantModel.User || !MerchantModel.User.email) {
			return localforage.getItem("user").then(function(user) {
				if (user != null) {
					MerchantModel.User = user;
					m.redraw();
					return;
				}
				MerchantModel.User = null;
				m.redraw();
			});
		}

	},
	Logout: function() {
		location.assign("/authenticate/");
		localforage.removeItem("user");
		MerchantModel.User = {};
		deleteCookie("jwt");
	},

};



